                </view>


                <!-- Êó∂Èó¥ËåÉÂõ¥ÈÄâÊã©Âô® - ËøòÂéüÊ≠£Â∏∏Ê†∑Âºè -->
                <view class="filter-item">
                    <uni-datetime-picker ref="datetimePicker" type="datetimerange" v-model="selectedTimeRange"
                        rangeSeparator="Ëá≥" start="2020-01-01 00:00:00" :end="getCurrentDateTime()"
