                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÈáçÈáèÔºö</text>
                                <text class="value">{{ item.weight ?? 0 }} kg</text>
                            </view>
                            <view class="info-item">
                                <text class="label">ËΩ¶ËæÜ‰ø°ÊÅØÔºö</text>
                                <text class="value">{{ item.registrationNumber ?? "ÊöÇÊó†" }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">È¢Ñ‰º∞Êó∂Èó¥Ôºö</text>
                                <text class="value">{{ item.appointmentTime ?? "ÊöÇÊó†" }}</text>
                            </view>
                            <view class="info-item">
                                <text class="label">Êî∂ËøêÊó∂Èó¥Ôºö</text>
                                <text class="value">{{ item.arrivalTime ?? "ÊöÇÊó†" }}</text>
                            </view>
                        </view>
                        <view class="order-footer">
                            <uni-button v-if="item.status != 0" size="mini" type="default" class="btn-confirm">
                                Êü•ÁúãËØ¶ÊÉÖ
                            </uni-button>
                        </view>
                    </view>

                    <!-- Âä†ËΩΩÊõ¥Â§öÁªÑ‰ª∂ - Âè™Âú®ÊúâÊï∞ÊçÆÊó∂ÊòæÁ§∫ -->
                    <view class="loadMore">
                        <uni-load-more :status="loadingStatus" :content-text="{
                            contentdown: '‰∏äÊãâÊòæÁ§∫Êõ¥Â§ö',
                            contentrefresh: 'Ê≠£Âú®Âä†ËΩΩ...',
                            contentnomore: 'Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü'
                        }"></uni-load-more>
                    </view>
                </view>

                <!-- ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ -->
                <view class="empty-state" v-else-if="loadingStatus !== 'loading'">
                    <view class="empty-icon">üìã</view>
                    <text class="empty-text">ÊöÇÊó†Êï∞ÊçÆ</text>
                    <text class="empty-desc">ÂΩìÂâçÂàÜÁ±ª‰∏ãÊöÇÊó∂Ê≤°ÊúâÁõ∏ÂÖ≥‰ø°ÊÅØ</text>
                </view>
            </scroll-view>
        </view>
    </view>
</template>
<script setup>
import {
    ref,
    computed,
    onMounted
} from 'vue';
import {
    onPullDownRefresh,
    onReachBottom
} from '@dcloudio/uni-app';

import {
    apiGetPlanAllPage
} from '@/api/apis.js';

import { useUserStore } from '@/stores/user.js'




const userStore = useUserStore();

// Á≠õÈÄâÁõ∏ÂÖ≥Áä∂ÊÄÅ
const selectedStatus = ref(''); // ÈÄâ‰∏≠ÁöÑÁä∂ÊÄÅ
const selectedTimeRange = ref([]); // ÈÄâ‰∏≠ÁöÑÊó∂Èó¥ËåÉÂõ¥

// Áä∂ÊÄÅÈÄâÈ°πÈÖçÁΩÆ (0 ÂæÖÁ°ÆËÆ§ 1 Â∑≤ÂÆåÊàê 2 Êó†ÈúÄÊî∂Ëøê)
const statusOptions = ref([
    { value: '0', text: 'ÂæÖÁ°ÆËÆ§' },
    { value: '1', text: 'Â∑≤ÂÆåÊàê' },
    { value: '2', text: 'Êó†ÈúÄÊî∂Ëøê' }
]);

// Áä∂ÊÄÅËΩ¨Êç¢ÂáΩÊï∞
const getStatusText = (status) => {
    console.log(123)
    switch (status) {
        case 0:
        case '0':
            return 'ÂæÖÁ°ÆËÆ§';
        case 1:
        case '1':
            return 'Â∑≤ÂÆåÊàê';
        case 2:
        case '2':
            return 'Êó†ÈúÄÊî∂Ëøê';
        default:
            return 'Êú™Áü•Áä∂ÊÄÅ';
    }
};

// Ëé∑ÂèñÁä∂ÊÄÅÊ†∑ÂºèÁ±ªÂêç
const getStatusClass = (status) => {
    switch (status) {
        case 0:
        case '0':
            return 'booking'; // ÂæÖÁ°ÆËÆ§ - ËìùËâ≤
        case 1:
        case '1':
            return 'completed'; // Â∑≤ÂÆåÊàê - ÁÅ∞Ëâ≤
        case 2:
        case '2':
            return 'processing'; // Êó†ÈúÄÊî∂Ëøê - ÁªøËâ≤
        default:
            return 'completed';
    }
};
//ËøîÂõû‰∏ä‰∏ÄÈ°µ
const back = () => {
    uni.navigateBack()
}

// Ê∑ªÂä†È°µÁ†ÅÂíåÂä†ËΩΩÁä∂ÊÄÅÂèòÈáè
const pageNum = ref(1);
const loadingStatus = ref('more'); // more-Âä†ËΩΩÂâç/loading-Âä†ËΩΩ‰∏≠/nomore-Ê≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ

// Êï∞ÊçÆÂàóË°®
const allOrderList = ref([]);

//ÂêéËé∑ÂèñÊï∞ÊçÆ
const getNetwork = async () => {
    try {
        // Â¶ÇÊûú‰∏çÊòØÁ¨¨‰∏ÄÈ°µÔºåËÆæÁΩÆÂä†ËΩΩÁä∂ÊÄÅ‰∏∫Âä†ËΩΩ‰∏≠
        if (pageNum.value > 1) {
            loadingStatus.value = 'loading';
        }

        // ÊûÑÂª∫ËØ∑Ê±ÇÂèÇÊï∞
        const params = {
            pageNum: pageNum.value,
            merchantId: 448,
        };

        // Ê∑ªÂä†Á≠õÈÄâÊù°‰ª∂
        if (selectedStatus.value !== '') {
            params.status = selectedStatus.value;
        }

        if (selectedTimeRange.value && selectedTimeRange.value.length === 2) {
            params.startTime = selectedTimeRange.value[0];
            params.endTime = selectedTimeRange.value[1];
        }

        const res = await apiGetPlanAllPage(params);


        // Â§ÑÁêÜ‰∏ãÊãâÂà∑Êñ∞
        if (pageNum.value === 1) {
            allOrderList.value = res.data.list || [];
            uni.stopPullDownRefresh();
        } else {
            // Â§ÑÁêÜ‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
            allOrderList.value = [...allOrderList.value, ...(res.data.list || [])];
        }

        // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
        if (res.data.list && res.data.list.length < 10) {
            // Â¶ÇÊûúËøîÂõûÁöÑÊï∞ÊçÆÂ∞ë‰∫éÊØèÈ°µÊï∞ÈáèÔºåËØ¥ÊòéÊ≤°ÊúâÊõ¥Â§öÊï∞ÊçÆ‰∫Ü
            loadingStatus.value = 'nomore';
        } else {
            // Âê¶ÂàôËøòÊúâÊõ¥Â§öÊï∞ÊçÆ
            loadingStatus.value = 'more';
        }
    } catch (error) {
        console.error('Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•:', error);

        // ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
        uni.stopPullDownRefresh();

        // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
        loadingStatus.value = 'more';

        // Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÈ°µÂä†ËΩΩÂ§±Ë¥•ÔºåÁ°Æ‰øùÊòæÁ§∫ÊöÇÊó†Êï∞ÊçÆÁä∂ÊÄÅ
        if (pageNum.value === 1) {
            allOrderList.value = [];
        }

        uni.showToast({
            title: 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
            icon: 'none',
            duration: 2000
        });
    }
};


// ‰∏äÊãâÂä†ËΩΩÊõ¥Â§öÊñπÊ≥ï
const onLoadMore = () => {
    if (loadingStatus.value === 'nomore') return;
    // È°µÁ†ÅÂ¢ûÂä†
    pageNum.value++;
    getNetwork();
};


// Ëß¶Â∫ïÂä†ËΩΩÊõ¥Â§ö
onReachBottom(() => {
    onLoadMore();
});

//‰∏ãÊãâÂà∑Êñ∞
onPullDownRefresh(() => {
    allOrderList.value = [];
    pageNum.value = 1; // ÈáçÁΩÆÈ°µÁ†Å‰∏∫1
    getNetwork();
})

// ÂºïÁî®ÈÄâÊã©Âô®ÁªÑ‰ª∂
const datetimePicker = ref(null);

// Á≠õÈÄâÁõ∏ÂÖ≥ÊñπÊ≥ï
const showStatusPicker = () => {
    uni.showActionSheet({
        itemList: statusOptions.value.map(item => item.text),
        success: (res) => {
            const selectedOption = statusOptions.value[res.tapIndex];
            onStatusChange(selectedOption.value);
        }
    });
};

const onStatusChange = (value) => {
    selectedStatus.value = value;
    // ÈáçÁΩÆÈ°µÈù¢Âπ∂ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
    resetPageAndReload();
};


const onTimeChange = (value) => {
    selectedTimeRange.value = value;
    // ÈáçÁΩÆÈ°µÈù¢Âπ∂ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
    resetPageAndReload();
};


const resetPageAndReload = () => {
    allOrderList.value = [];
    pageNum.value = 1;
    getNetwork();
};


// Ëé∑ÂèñÂΩìÂâçÊó•ÊúüÊó∂Èó¥ÔºàÂåÖÂê´Êó∂ÂàÜÁßíÔºâ
const getCurrentDateTime = () => {
    const now = new Date();
    const year = now.getFullYear();
    const month = String(now.getMonth() + 1).padStart(2, '0');
    const day = String(now.getDate()).padStart(2, '0');
    const hour = String(now.getHours()).padStart(2, '0');
    const minute = String(now.getMinutes()).padStart(2, '0');
    const second = String(now.getSeconds()).padStart(2, '0');
    return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
};


// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÂàùÂßãÂåñÊï∞ÊçÆ
onMounted(() => {
    pageNum.value = 1;
    getNetwork();
});
</script>

